.my-lib__hero {
  max-width: 480px;
  height: 380px;
  padding-top: 40px;

  margin-left: auto;
  margin-right: auto;

  background-image: url(../images/mobile/hero-b-mob.png),
    linear-gradient(87.8deg, #0e0e0e 15.61%, rgba(14, 14, 14, 0) 60.39%),
    url(../images/mobile/library-bg-mob.jpg);

  @include retina {
    background-image: url(../images/mobile/hero-b-mob-2x.png),
      linear-gradient(87.8deg, #0e0e0e 15.61%, rgba(14, 14, 14, 0) 60.39%),
      url(../images/mobile/library-bg-mob-2x.jpg);
  }
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  @include tablet {
    max-width: 768px;
    height: 372px;
    padding-top: 62px;

    background-image: url(../images/tablet/hero-b-tab.png),
      linear-gradient(87.8deg, #0e0e0e 15.61%, rgba(14, 14, 14, 0) 60.39%),
      url(../images/tablet/library-bg-tab.jpg);

    @include retina {
      background-image: url(../images/tablet/hero-b-tab-2x.png),
        linear-gradient(87.8deg, #0e0e0e 15.61%, rgba(14, 14, 14, 0) 60.39%),
        url(../images/tablet/library-bg-tab-2x.jpg);
    }
  }

  @include desktop {
    max-width: 1280px;
    height: 660px;
    padding-top: 169px;

    background-image: url(../images/desktop/hero-b-des.png),
      linear-gradient(87.8deg, #0e0e0e 15.61%, rgba(14, 14, 14, 0) 60.39%),
      url(../images/desktop/library-bg-des.jpg);

    @include retina {
      background-image: url(../images/desktop/hero-b-des-2x.png),
        linear-gradient(87.8deg, #0e0e0e 15.61%, rgba(14, 14, 14, 0) 60.39%),
        url(../images/desktop/library-bg-des-2x.jpg);
    }
  }
}

.my-lib__hero-container {
  @include desktop {
    margin-left: 85px;
  }
}

.my-lib__hero-title {
  display: inline-block;
  max-width: 235px;
  margin-bottom: 8px;
  font-size: 32px;
  line-height: 1.18;
  @include tablet {
    max-width: 332px;
    font-size: 44px;
  }
  @include desktop {
    max-width: 464px;
    margin-bottom: 16px;
    font-size: 64px;
    line-height: 1.17;
  }
}

.my-lib__hero-description {
  display: block;
  max-width: 249px;
  font-size: 14px;
  line-height: 1.14;
  @include tablet {
    max-width: 357px;
    font-size: 16px;
  }
  @include desktop {
    max-width: 464px;
    line-height: 1.18;
  }
}
