.film-card {
  min-width: 280px;
  min-height: 402px;
  border-radius: 5px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1), box-shadow .25s cubic-bezier(.4, 0, .2, 1);
  position: relative;
  overflow: hidden;
  transform: scale(1);
}

.film-card:after {
  content: "";
  width: 100%;
  height: 100%;
  background-image: linear-gradient(rgba(0, 0, 0, 0) 63.48%, rgba(0, 0, 0, .9) 92.16%);
  position: absolute;
  top: 0;
  left: 0;
}

